<template>
  <div v-show="list.length">
    <div class="list-control">
      <div class="list-control-filter">
        <span>品牌：</span>
        <span
          class="item-control-filter-item"
          :class="{on: item === filterBrand}"
          v-for="(item,index) in brands"
          @click="handleFilterBrand(item)"
          :key="index"
         >
        {{ item }}
        </span>
      </div>
      <div class="list-control-filter">
        <span>颜色：</span>
        <span
            class="item-control-filter-item"
            :class="{on:item === filterColor}"
          v-for="(item, index) in colors"
          @click="handleFilterColor(item)"
          :key="index"
          >
          {{ item }}
        </span>
      </div>
      <div class="list-control-order">
        <span>排序：</span>
        <span
          class="list-control-order-item"
          :class="{on: order===''}"
          @click="handleOrderDefault"
          >
          默认
        </span>
        <span class="list-control-order-item"
          :class="{on: order === 'sales'}"
          @click="handleOrderSales"
          >
          销量
          <template v-if="order === 'sales'">↓</template>
        </span>
        <span
          class="list-control-order-item"
          :class="{on : order.indexOf('cost') > -1 }"
          @click="handleOrderCost"
          >
          价格
          <template v-if="order === 'cost-asc'">↑</template>
          <template v-if="order === 'cost-desc'">↓</template>
        </span>
      </div>
    </div>
    <Product
         v-for="item in filteredList"
         :info="item"
         :key="item.id">
    </Product>
    <div class="product-not-found" v-show="!filteredList.length">
        暂无相关商品
    </div>
  </div>
</template>

<script>
import Product from './product.vue'

export default {
    components: {
        Product
    },
    data () {
        return {
            order: '',
            filterBrand: '',
            filterColor: ''
        }
    },
    methods: {
        handleOrderSales () {
            this.order = 'sales'
        },
        handleOrderCost () {
            if (this.order === 'cost-desc') {
                this.order = 'cost-asc'
            } else {
                this.order = 'cost-desc'
            }
        },
        handleOrderDefault () {
            this.order = ''
        },
        handleFilterBrand (item) {
            // 单次点击选中，再次点击取消选中
            if (this.filterBrand === item) {
                this.filterBrand = ''
            } else {
                this.filterBrand = item
            }
        },
        handleFilterColor (item) {
            if (this.filterColor === item) {
                this.filterColor = ''
            } else {
                this.filterColor = item
            }
        }
    },
    computed: {
        filteredList () {
            // 复制原始数据
            let list = [...this.list]
            // 按品牌过滤
            if (this.filterBrand !== '') {
                list = list.filter(item => item.brand === this.filterBrand)
            }
            // 按颜色过滤
            if (this.filterColor !== '') {
                list = list.filter(item => item.color === this.filterColor)
            }
            if (this.order !== '') {
                if (this.order === 'sales') {
                    list = list.sort((a, b) => b.sales - a.sales)
                } else if (this.order === 'cost-desc') {
                    list = list.sort((a, b) => b.cost - a.cost)
                } else if (this.order === 'cost-asc') {
                    list = list.sort((a, b) => a.cost - b.cost)
                }
            }
            return list
        },
        list () {
            return this.$store.state.productList
        },
        brands () {
            return this.$store.getters.brands
        },
        colors () {
            return this.$store.getters.colors
        }
    },
    mounted () {
        this.$store.dispatch('getProductList')
    }
}
</script>

<style>
.product-not-found{
  text-align: center;
  padding: 32px;
}

.list-control{
  background: #fff;
  border-radius: 6px;
  margin: 16px;
  padding: 16px;
  box-shadow: 0 1px 1px rgba(0,0,0,.2);
}

.list-control-filter{
    margin-bottom: 16px;
}
.list-control-order-item,
.item-control-filter-item,
.item-control-order-item
{
    cursor: pointer;
    display: inline-block;
    border: 1px solid #e9eaec;
    border-radius: 4px;
    margin-right: 6px;
    padding: 2px 6px;
}
.list-control-order-item.on,
.item-control-filter-item.on,
.item-control-order-item.on
{
   background: #f2352e;
   border: 1px  solid #f2352e;
   color: #fff;
}

</style>



